import Qs from 'qs'
import axios from 'axios'
import requestMap from './config'

let [baseOrigin, origin] = ['', `${window.location.protocol}//${window.location.host}`];
let match = origin.match(/^(http:\/\/(127.0.0.1|192.168.1.|localhost))\d*:\d{2,4}/i);
baseOrigin = match && match[1];
baseOrigin = baseOrigin || origin;
let requestItem = (baseOrigin && requestMap[baseOrigin]) || requestMap['https://ymmsit.verify.fc18.com.cn'];

baseOrigin = !!requestItem ? requestItem.requestUrl : '';

let xhrHeaders = {'Content-Type':'application/json; charset=utf-8'};
if (requestItem) {
    xhrHeaders.AppId = requestItem.appId;
    xhrHeaders.AppSecret = requestItem.appSecret;
}
let isregister=window.location.href.indexOf('register.html');
// 
// axios.interceptors.request.use((config) => {
//     let accessToken = window.sessionStorage.getItem('accessToken') || window.localStorage.getItem('accessToken');
//     if (accessToken) {
//         config['headers']['x-auth-token'] = accessToken;
//     }

//     if (config.loading) {
//         //TODO startLoading
//     }
//     return config;
// }, (error) => {
//     //
//     //TODO stopLoading
//     return Promise.reject(error);
// });

//
axios.interceptors.response.use((response) => {
    //TODO stopLoading
    return response;
}, (error) => {
    //
    //TODO stopLoading
    error = error && error.response;
    if (!error) {
        return false;
    }
    
    //400 Bad Request
    if (error.status == '400') {
        return Promise.reject(error);
    }
    //401 Unauthorized
    if (error.status == '401') {
        //
        //dev 
        //
        let path = window.location.pathname.split('/');
        if (window.location.href.indexOf('/user/register.html') === -1) {
            window.sessionStorage.setItem('preAuthUrl', window.location.href);  
        }
        window.location.href = (path[2] && path[2] === 'issue') ?
        `/${path[1]}/issue/user/login.html` :
        `/user/login.html`;
        return Promise.reject(error);
    }
});

export default {
    method: 'GET',

    baseURL: baseOrigin + '/ymmopenapi/sgw/v1',

    url: '',

    //
    //put/post/patch
    //stringArrayBufferStream
    transformRequest: [data => data],

    transformResponse: [data => data],

    headers: xhrHeaders,

    params: {},

    paramsSerializer: (params) => Qs.stringify(params),

    data: {},

    timeout: 60000,

    //
    withCredentials: false,

    responseType: 'json',

    onUploadProgress: function (progressEvent) {
	},

    //download
	onDownloadProgress: function (progressEvent) {
	},

    //response
	maxContentLength: 2000,

    //default HTTPpromise validateStatus  true ( null  undefined ),promise;,promise 
	validateStatus: function (status) {
	    return status >= 200 && status < 300;
	},

    // default  Node.jsredirect0redirect 
	maxRedirects: 5, 

    //
    loading: true
}



